.homepage{
    
}

.covid-test-packages{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.cart-item {
    width:25%;
    padding: 16px;
    position: relative;
    transition: box-shadow .2s ease-in-out;
    box-shadow: 6px 6px 6px #80808026;
}

@media (max-width: 1140px){
    .cart-item {
        width:33.33%;
        
    }
}
.prod-container{
    padding-bottom: 16px;
    transition: opacity .3s;
    opacity: 1;
}
.covid-prod{
    height: 200px;
    width: 200px;
    position: relative;
    margin: 0 auto;
}
.covid-prod-img{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
}
.prd-cnt{
    display: flex;
    justify-content: space-between;
    padding: 8px 0px;
    align-items: center;
}
.prod-price{
    display: inline-block;
    font-size: 16px;
    font-weight: 500;
    color: #212121;
}
.addtoCart{
    padding: 6px;
    background: gold;
    border-radius: 5px;
    border: 1px solid #80808070;
    cursor: pointer;
}